import React                                        from 'react';
import {
    connect
}                                                   from 'react-redux';




/* ################################################### */
/*          ui framework         */
/* ################################################### */

import {
    Grid , Row , Col
}                                                   from 'react-bootstrap';
import {
    Table,
    TableBody,
    TableHeader,
    TableHeaderColumn,
    TableRow, TableRowColumn }                      from 'material-ui/Table';
import TextField                                    from 'material-ui/TextField';
import {
  Step,
  Stepper,
  StepLabel,
}                                                   from 'material-ui/Stepper';
import Paper                                        from 'material-ui/Paper';
import FlatButton                                   from 'material-ui/FlatButton';
import RaisedButton                                 from 'material-ui/RaisedButton';
import Divider                                      from 'material-ui/Divider';
import Dialog                                       from 'material-ui/Dialog';

import SelectField                                  from 'material-ui/SelectField';
import MenuItem                                     from 'material-ui/MenuItem';
import DatePicker                                   from 'material-ui/DatePicker';
import TimePicker                                   from 'material-ui/TimePicker';






/* ################################################### */
/*          redux         */
/* ################################################### */

import ReduxActionSettingDowntime , { ReduxEnumSettingDowntime }  from '../../../_redux/action/reduxAction.settingDowntime';









/* ################################################### */
/*          helper         */
/* ################################################### */
import HelperNavigate , { HelperNavigateENUM }		from '../../_helper/h.navigate';
import HelperStorage		                        from '../../_helper/h.storage';










/* ################################################### */
/*          abstract         */
/* ################################################### */

import ComponentAbstract                            from '../../_abstract/c.abstract';










/* ################################################### */
/*          component         */
/* ################################################### */

import ComponentCustomLoading                       from '../../_component/loading/cc.loading';

import ComponentSettingDowntimeStep1                    from './step1/c.settingDowntime.step1';
import ComponentSettingDowntimeStep2                    from './step2/c.settingDowntime.step2';
import ComponentSettingDowntimeStep3                    from './step3/c.settingDowntime.step3';









import './s.settingDowntime.css';







export default connect(
        ( state ) => {

            let thisState = state[ 'ReduxSettingDowntime' ] ;
            let stateObj = { };

            if ( thisState ) {

                for ( let i = 0 ; i < thisState.length; i ++ ) {
                    let recordItem = thisState[ i ];
                    if ( !recordItem[ 'completed' ] ) {

                        if ( ReduxEnumSettingDowntime.INIT_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestInitDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];


                        } else if ( ReduxEnumSettingDowntime.UPDATE_PREVIEW_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdatePreviewDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        } else if ( ReduxEnumSettingDowntime.UPDATE_CONFIRM_DONE === recordItem[ 'type' ] ) {
                            stateObj[ 'onReadyFunction' ]       = 'requestUpdateConfirmDone';
                            stateObj[ '_result_' ]              = recordItem[ '_result_' ];

                        }

                        recordItem[ 'completed' ] = true;
                    }
                }

            }

            return stateObj;

        }
    ) (

        class ComponentSettingDowntime extends ComponentAbstract {






            constructor( props ) {
                super( props )

                this.state = {

                    step : 0

                    , data : {}


                    , form : {
                    }


                    , result : {
                        preview     : {}

                        , confirm   : {}
                    }

                }

            }









            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        listener
                ################################################################################################################################################
                ################################################################################################################################################
            */

            _onReady() {
                this.requestInit();
            }











            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        request
                ################################################################################################################################################
                ################################################################################################################################################
            */

            requestInit() {
                console.log( 'ComponentSettingDowntime : requestInit : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionSettingDowntime.rInit(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestInitDone( result ) {
                console.log( 'ComponentSettingDowntime : requestInitDone : ***  : result => ' , result );

                let data = this.state.data;
                this.setState(
                    {
                        isLoading   : false
                        , data      : data
                    }
                );
                this.dispatch( ReduxActionSettingDowntime.rRequestEnd( ) );
            }







            requestUpdatePreview() {
                console.log( 'ComponentSettingDowntime : requestUpdatePreview : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionSettingDowntime.rUpdatePreview(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , { }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdatePreviewDone( result ) {
                console.log( 'ComponentSettingDowntime : requestUpdatePreviewDone : ***  : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'preview' ] = result;
                this.setState(
                    {
                        isLoading       : false
                        , result        : resultObj

                        , step          : 1
                    }
                );
                this.dispatch( ReduxActionSettingDowntime.rRequestEnd( ) );
            }

            requestUpdateConfirm() {
                console.log( 'ComponentSettingDowntime : requestUpdateConfirm : *** ' );

                Promise.all( [
                    ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_TOKEN_TYPE )
                    , ( new HelperStorage() ).get( HelperStorage.ENUM.SESSION , HelperStorage.KEY.AUTHORIZATION_ACCESS_TOKEN )
                ] ).then(
                    ( value ) => {

                        this.setState(
                            { isLoading : true }
                            , () => {
                                this.dispatch(
                                    ReduxActionSettingDowntime.rUpdateConfirm(
                                        { authorizeType : value[ 0 ] , authorizeToken : value[ 1 ] }
                                        , {
                                            instructionId : this.state.result.preview.instructionId
                                        }
                                    )
                                )
                            }
                        );

                    }
                )
            }
            requestUpdateConfirmDone( result ) {
                console.log( 'ComponentSettingDowntime : requestUpdateConfirmDone : ***  : result => ' , result );

                let resultObj = this.state.result;
                resultObj[ 'confirm' ] = result;
                this.setState(
                    {
                        isLoading       : false
                        , result        : resultObj

                        , step          : 2
                    }
                );
                this.dispatch( ReduxActionSettingDowntime.rRequestEnd( ) );
            }












            /*
                ################################################################################################################################################
                ################################################################################################################################################
                        render
                ################################################################################################################################################
                ################################################################################################################################################
            */

            render() {
                return (
                        <div>
                            <Paper
                                    style={ { width : '70%', margin : '0px auto' } }>


                                <div className={ ' settingDowntime-padding-10 ' }>
                                    <h3> Downtime Maintenance </h3>
                                </div>


                                <div className={ ' settingDowntime-margin-auto settingDowntime-paddingTop-20 settingDowntime-paddingBottom-20  settingDowntime-maxWidth-900 ' }>

                                    <Stepper activeStep={ this.state.step }>

                                        <Step>
                                            <StepLabel>
                                                Downtime Maintenance
                                            </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Preview </StepLabel>
                                        </Step>

                                        <Step>
                                            <StepLabel> Confirmation </StepLabel>
                                        </Step>

                                    </Stepper>

                                </div>


                                <div>

                                    {
                                        ( () => {

                                            switch ( this.state.step ) {

                                                case 0:

                                                    return (
                                                        <ComponentSettingDowntimeStep1
                                                                    data={ this.state.data }

                                                                    form={ this.state.form }


                                                                    onChangeForm={
                                                                        ( key , value ) => {

                                                                        }
                                                                    }

                                                                    onClickNext={ () => {
                                                                        this.requestUpdatePreview();
                                                                    } }
                                                                    onClickBack={ () => {

                                                                    } }
                                                        />
                                                    );

                                                    break;

                                                case 1:

                                                    return (
                                                        <ComponentSettingDowntimeStep2
                                                                    data={ this.state.data }

                                                                    form={ this.state.form }

                                                                    result={ this.state.result }


                                                                    onClickNext={ () => {
                                                                        this.requestUpdateConfirm();
                                                                    } }
                                                                    onClickBack={ () => {
                                                                        this.setState( { step : 0 } );
                                                                    } }
                                                        />
                                                    );

                                                    break;

                                                case 2:

                                                    return (
                                                        <ComponentSettingDowntimeStep3
                                                                    data={ this.state.data }

                                                                    form={ this.state.form }

                                                                    result={ this.state.result }


                                                                    onClickNext={ () => {

                                                                    } }
                                                                    onClickBack={ () => {

                                                                    } }
                                                        />
                                                    );

                                                    break;

                                                default:
                                                    return ( null );
                                            }

                                        } )()
                                    }

                                </div>

                            </Paper>


                            <ComponentCustomLoading
                                visible={ this.state.isLoading }
                            />

                        </div>
                )
            }
        }

    )



